<template>
  <div>
    <el-dialog :title="title" width="30%" :visible.sync="freezestatedialog">
      <span class="freeze_num">{{$t('storage.freezestate')}}:</span>
      <span class="freeze_num freezestate">{{freezestate}}</span>

      <div class="freeze_btn">
        <el-button type="danger" @click="freeze" :disabled="state == -1? true:false">{{$t('storage.freeze')}}</el-button>
        <el-button type="primary" @click="unfreeze" :disabled="state == -1? false:true">{{$t('storage.unfreeze')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {
    searchByLabelCode,
    mateFreezeUnfreeze
  } from '@/request/api/material'
  export default {
    data() {
      return {
        freezestatedialog: false,
        title: this.$t('storage.label_code'),
        state: '',
        label_code: ''
      }
    },
    computed: {
      freezestate: function() {
        if (this.state == -1) {
          return this.$t('storage.freeze')
        } else {
          return this.$t('storage.unfreeze')
        }
      }
    },
    methods: {
      getFreezeState(value) {
        searchByLabelCode({
          label_code: value
        }).then(res => {
          if (res.result) {
            if (Object.keys(res.mate_info).length) {
              this.freezestatedialog = true
              this.title = this.$t('storage.label_code') + ':' + value
              this.state = res.mate_info.state
              this.label_code = res.mate_info.label_code
            } else {
              this.$message({
                showClose: true,
                message: this.$t('common.nodata'),
                type: 'warning'
              })
            }
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      freeze() {
        mateFreezeUnfreeze({
          op_type: 0,
          label_code: this.label_code
        }).then(res => {
          if (res.result) {
            this.freezestatedialog = false
            this.$message({
              showClose: true,
              message: this.$t('storage.freezesuccess'),
              type: 'success'
            })
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      unfreeze() {
        mateFreezeUnfreeze({
          op_type: 1,
          label_code: this.label_code
        }).then(res => {
          if (res.result) {
            this.freezestatedialog = false
            this.$message({
              showClose: true,
              message: this.$t('storage.unfreezesuccess'),
              type: 'success'
            })
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      }
    }
  }
</script>
<style>
  .freeze_num {
    font-size: 18px;
  }

  .freeze_btn {
    margin-top: 20px;
  }

  .freezestate {
    padding-left: 20px;
  }
</style>



