<template>
  <div>
    <div class="update_div">
      <span class="freeze_update_title">{{$t('storage.freezetitle')}}</span>
      <el-divider></el-divider>
      <div class="update_num">
        <span class="freeze_title">{{$t('storage.label_code')}}</span>
        <el-input v-model="label_code" clearable></el-input>
      </div>
      <div class="update_btn">
        <el-button type="primary" @click="searchFreeze">{{$t('common.search')}}</el-button>
      </div>
      <!-- <v-freezestate ref="freezestate"></v-freezestate> -->
    </div>
    <!-- 唯一单据编号 -->
    <div class="update_div">
      <span class="freeze_update_title">{{$t('storage.orderFreezetitle')}}</span>
      <el-divider></el-divider>
      <div class="update_num">
        <span class="freeze_title">{{$t('storage.order_code')}}</span>
        <el-input v-model="order_code" clearable></el-input>
      </div>
      <div class="update_btn">
        <el-button type="primary" @click="searchOrderCode">{{$t('common.search')}}</el-button>
      </div>
      <v-freezestate ref="freezestate"></v-freezestate>
      <v-freezelist ref="freezelist" :orderCode="order_code"></v-freezelist>
    </div>
  </div>
</template>
<script>
  import VFreezestate from './freezestate'
  import VFreezelist from './freezelist'
  export default {
    components: {
      VFreezestate,
      VFreezelist
    },
    data() {
      return {
        label_code: '',
        order_code: '',
      }
    },
    methods: {
      searchFreeze() {
        this.$refs.freezestate.getFreezeState(this.label_code)
      },
      searchOrderCode() {
        this.$refs.freezelist.getFreezelist(this.order_code)
      }
    }

  }
</script>
<style scoped>
  .update_div {
    margin: 50px;
    padding: 40px;
    border: 1px solid lightgray;
    text-align: left;
    border-radius: 6px;
  }

  .update_num {
    display: flex;
    height: 60px;
    line-height: 60px;
  }

  .update_num .el-input {
    width: 240px;
  }

  .freeze_update_title {
    display: block;
    width: 160px;
    font-size: 18px;
  }

  .freeze_title {
    display: block;
    width: 120px;
    font-size: 18px;
  }

  .update_btn {
    margin-top: 10px;
    margin-left: 120px;
  }
</style>



