<template>
  <div>
    <el-dialog :title="title" width="50%" :visible.sync="freezelistdialog">
      <el-table :header-cell-style="{background:'#DFEBF9',color:'#606266'}" :data="freezelist" :empty-text="$t('common.nodata')"
        height="260px" border>
        <el-table-column prop="part_num" :label="$t('storage.part_num')">
        </el-table-column>
        <el-table-column prop="label_code" :label="$t('storage.label_code')">
        </el-table-column>
        <el-table-column prop="quantity" :label="$t('storage.quantity')">
        </el-table-column>
        <el-table-column prop="state" :formatter="stateFormatter" :label="$t('storage.state')">
        </el-table-column>
        <!-- 	<el-table-column
					prop="num"
					width="100px"
				  :label="$t('common.operation')">
				  <template slot-scope="scope">
				  	<el-button v-if="scope.row.state" type="primary" siz="mini" class="freeze_opt_btn">{{$t('storage.unfreeze')}}</el-button>
				  	<el-button v-else="scope.row.state" type="danger" siz="mini" class="freeze_opt_btn">{{$t('storage.freeze')}}</el-button>
				  </template>
				</el-table-column>	 -->
      </el-table>
      <div class="freeze_btn">
        <el-button type="danger" @click="orderFreeze">{{$t('storage.freeze')}}</el-button>
        <el-button type="primary" @click="orderUnfreeze">{{$t('storage.unfreeze')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {
    searchByOrderId,
    mateFreezeUnfreezeByOrderId
  } from '@/request/api/material'
  export default {
    data() {
      return {
        freezelistdialog: false,
        title: '',
        freezelist: [],
        order_id: ''
      }
    },
    methods: {
      stateFormatter(value) {
        return value.state ? '冻结' : '解冻'
      },
      getFreezelist(value) {
        this.order_id = value
        this.title = this.$t('storage.order_code') + ':' + value
        searchByOrderId({
          order_id: value
        }).then(res => {
          if (res.result) {
            if (res.mate_list.length) {
              this.freezelist = res.mate_list
              this.freezelistdialog = true
            } else {
              this.$message({
                showClose: true,
                message: this.$t('common.nodata'),
                type: 'warning'
              })
            }
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })

      },
      orderFreeze() {
        mateFreezeUnfreezeByOrderId({
          order_id: this.order_id,
          op_type: 0
        }).then(res => {
          if (res.result) {
            this.freezelistdialog = false
            this.$message({
              showClose: true,
              message: this.$t('storage.freezesuccess'),
              type: 'success'
            })
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      },
      orderUnfreeze() {
        mateFreezeUnfreezeByOrderId({
          order_id: this.order_id,
          op_type: 1
        }).then(res => {
          if (res.result) {
            this.freezelistdialog = false
            this.$message({
              showClose: true,
              message: this.$t('storage.unfreezesuccess'),
              type: 'success'
            })
          } else {
            this.$message({
              showClose: true,
              message: res.msg,
              type: 'error'
            })
          }
        })
      }
    }

  }
</script>
<style scoped>
  .freeze_opt_btn {
    height: 28px;
    padding: 7px 15px;
  }
</style>



